    // mixin+vuex +
    const mixin = {
        mounted() {
            this.$store.commit("setPageName", this.pageName)
                // console.log('mounted')
        },
        activated() {
            this.$store.commit("setPageName", this.pageName)
                // console.log('activated')
        }
    }
    export default mixin


