<template>
  <div>
    <welcome></welcome>
    <div class="outter" :class="{'hideLeft':$route.path.split('/').length>2}">
      <!---->
      <header class="app-header" :class="{'header-hide':!$store.state.headerStatus}">
        <wx-header :pageName="pageName"></wx-header>
      </header>
      <!-- -->
      <search v-show="$route.path.indexOf('explore')===-1&&$route.path.indexOf('self')===-1"></search>
      <section class="app-content">
        <div id="wechat">
          <ul class="wechat-list">
          <msg-item v-for="baseMsgObj in $store.state.msgList.baseMsg" :item="baseMsgObj" class="list-row line-bottom" :key="baseMsgObj.mid"></msg-item>
          </ul>
        </div>
      </section>
      <footer class="app-footer">
        <wx-nav></wx-nav>
      </footer>
    </div>
  </div>
</template>
<script>
  import welcome from '../common/welcome.vue'
  import WxHeader from '../common/wx-header';
  import WxNav from '../common/wx-nav.vue';
  import search from '../common/search.vue';
  import msgItem from "../common/msg-item.vue";
  import mixin from "../../vuex/mixin.js" //  mixin.js 
  window.mixin = mixin //  /mixin  window.mixin
  export default {
    data() {
      return {
        "pageName": "",
        "routerAnimate": false,
        "enterAnimate": "", //
        "leaveAnimate": "" //
      }
    },
    watch: {
      //  $route 
      "$route" (to, from) {
        const toDepth = to.path.split('/').length
        const fromDepth = from.path.split('/').length
        if (toDepth === 2) {
          this.$store.commit("setPageName", to.name)
        }
        //
        if (toDepth === fromDepth) {
          return;
        }
        this.enterAnimate = toDepth > fromDepth ? "animated fadeInRight" : "animated fadeInLeft"
        this.leaveAnimate = toDepth > fromDepth ? "animated fadeOutLeft" : "animated fadeOutRight"
        //    name  router-view
        if (toDepth === 3) {
          this.leaveAnimate = "animated fadeOutRight"
        }
      }
    },
    components: {
      WxHeader,
      WxNav,
      search,
      msgItem,
      welcome
    }
  };
</script>
<style>
  @import "../../assets/css/base.css";
  @import "../../assets/css/common.css";
  @import "../../assets/css/wx-header.css";
  @import "../../assets/css/lib/iconfont.css";
  @import "../../assets/css/lib/animate.css";
  @import "../../assets/css/wechat.css";
  @import "../../assets/css/lib/weui.min.css";
</style>



